html {
  box-sizing: border-box;
}
body {
  background: $light-gray;
  font-size: $font-size;
  font-family: $font-family;
  box-sizing: border-box;
}

.container {
  max-width: 1440px;
  // width: 100vw;
  // height: 100vh;
  margin: 60px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

// Header

.header {
  text-align: center;
  h1 {
    margin-bottom: 1.389vw;
    font-size: 2.25em;
    font-weight: 200;
  }
  h2 {
    margin-bottom: 2.083vw;
    font-size: 2.25em;
    font-weight: 600;
  }
  p {
    margin: 0 auto 6.521739130434782vh auto; //left and right auto 60px in viewport 920px
    max-width: 500px;
    font-size: 1em; 
    color: $grayish-blue;
  }
}

// Cards

.cards {
  max-width: 1440px;
  display: grid;
  gap: 2.083vw; // 30px in viewport 1440px
  grid-template: 
  "    .      team          . " 1fr
  "supervisor team  calculator" 1fr
  "supervisor karma calculator" 1fr
  ".          karma          ." 1fr

  ;
}

.cards > div{
  border-radius: 8px;
  box-shadow: 1px 6px 16px $grayish-blue;
  border-top: 3px solid;
  padding: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  // min-width: 24.306vw; //350px in viewport 1440px
  // height: 27.173913043478258vh; //250px in viewport 920px
  width: 350px;
  height: 250px;
  span {
    font-size: 1.25em;
    font-weight: 600;
    margin-bottom: 20px;
  }
  p{
    color: $grayish-blue;
    margin-bottom: 40px;
  }
  img {
    display: block;
    align-self: flex-end;
    justify-self: flex-end;
  }
}

.cards div:nth-child(1){
  grid-area: supervisor;
  border-color: $cyan;
  }
.cards div:nth-child(2){
  grid-area: team;
  border-color: $red;
}
.cards div:nth-child(3){
  grid-area: karma;
  border-color: $orange;
}
.cards div:nth-child(4){
  grid-area: calculator;
  border-color: $blue;
}

// footer
.footer {
  margin-top: 8.695652173913043vh; // 80px in viewport 920px
}