@media screen and (max-width: 1220px){
  .cards {
    gap: 2.083vw; // 30px in viewport 1440px
    grid-template: 
    "supervisor team" 1fr
    "karma calculator" 1fr
    ;
  }
  .cards > div{
    width: 350px; 
    height: 250px; 
}
}
@media screen and (max-width: 920px){
  .cards {
    justify-content: center;
    gap: 30px;
    grid-template: 
    "supervisor" 1fr
    "team"       1fr
    "karma"      1fr
    "calculator" 1fr
    ;
  }
  .cards > div{
    width: 300px; 
    height: 250px; 
}
  .header {
    h1, h2 {
      font-size: 1.5rem;
    }
    p {
      width: 300px;
      margin-bottom: 30px;
    }
  }
  .footer {
    width: 300px;
    text-align: center;
  }
}